<template>
  <div id="app">
    <el-form inline>
      <el-form-item label="设备标识符">{{ device }}</el-form-item>
      <el-form-item>
        <el-select v-model="form.type" placeholder="选择任务">
          <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value"/>
        </el-select>
      </el-form-item>
      <el-form-item>
        <el-input v-if="form.type === 'Settings-Stage1'" v-model="form.params" placeholder="关卡名称"/>
      </el-form-item>
      <el-form-item>
        <el-button @click="addTask" type="primary">提交任务</el-button>
        <el-button @click="refresh" type="primary">刷新</el-button>
        <el-button @click="capture" type="primary">截图</el-button>
      </el-form-item>
    </el-form>
    <el-table border :data="tableData" v-loading="tableLoading">
      <el-table-column label="任务名称">
        <template slot-scope="scope">
          {{ taskMapper[scope.row.type] + (scope.row.params == null ? '' : scope.row.params) }}
        </template>
      </el-table-column>
      <el-table-column label="操作">
        <template slot-scope="scope">
          <el-button @click="removeTask(scope.row.id)" type="primary">删除任务</el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-image :src="'data:image/png;base64,' + image" fit="contain" v-loading="imageLoading"
              style="width: 100%; height: 720px; margin-top: 20px">
      <div slot="error" class="image-slot">
        <i class="el-icon-picture-outline"></i>
      </div>
    </el-image>
  </div>
</template>

<script>

import axios from "axios";
import Sortable from "sortablejs";

export default {
  name: 'app',
  data() {
    return {
      device: null,
      options: [
        {
          label: "开始唤醒",
          value: "LinkStart-WakeUp"
        },
        {
          label: "基建换班",
          value: "LinkStart-Base"
        },
        {
          label: "刷理智",
          value: "LinkStart-Combat"
        },
        {
          label: "自动公招",
          value: "LinkStart-Recruiting"
        },
        {
          label: "获取信用及购物",
          value: "LinkStart-Mall"
        },
        {
          label: "自动肉鸽",
          value: "LinkStart-AutoRoguelike"
        },
        {
          label: "生息演算·沙洲遗闻",
          value: "LinkStart-ReclamationAlgorithm"
        },
        {
          label: "一键长草",
          value: "LinkStart"
        },
        {
          label: "设置关卡",
          value: "Settings-Stage1"
        }
      ],
      taskMapper: {},
      tableData: [],
      tableLoading: true,
      form: {
        type: null,
        params: null
      },
      image: null,
      imageLoading: false
    }
  },
  created() {
    this.options.forEach(item => this.taskMapper[item.value] = item.label);
    this.device = localStorage.getItem('device');
    if (this.device != null) {
      return;
    }
    this.$prompt('请输入设备标识符', '提示', {
      confirmButtonText: '确定',
      showClose: false,
      showCancelButton: false
    }).then(({value}) => {
      localStorage.setItem("device", value);
      this.device = value;
    });
  },
  mounted() {
    this.taskList();
    Sortable.create(document.querySelector(".el-table__body-wrapper tbody"), {
      onEnd: (e) => {
        const taskId = this.tableData[e.oldIndex].id;
        const nextTaskId = e.newIndex === this.tableData.length - 1 ? null : this.tableData[e.newIndex + 1].id;
        this.tableLoading = true;
        axios.get("/api/moveTask", {
          params: {
            device: this.device,
            taskId: taskId,
            nextTaskId: nextTaskId
          }
        }).then(() => this.taskList());
      }
    });
  },
  methods: {
    taskList() {
      axios.get("/api/taskList", {
        params: {
          device: this.device
        }
      }).then(({data}) => {
        this.tableData = data;
        this.tableLoading = false;
      });
    },
    refresh() {
      this.tableLoading = true;
      this.taskList();
    },
    addTask() {
      if (!this.form.type) {
        this.$notify.error({
          title: "错误",
          message: "请选择任务"
        });
        return false;
      }
      if (this.form.type === 'Settings-Stage1' && !this.form.params) {
        this.$notify.error({
          title: "错误",
          message: "请输入关卡名称"
        });
        return false;
      }
      this.tableLoading = true;
      axios.get("/api/addTask", {
        params: {
          device: this.device,
          type: this.form.type,
          params: this.form.type === 'Settings-Stage1' ? this.form.params : null
        }
      }).then(() => this.taskList());
    },
    removeTask(id) {
      axios.get("/api/removeTask", {
        params: {
          device: this.device,
          task: id
        }
      }).then(() => this.taskList());
    },
    moveTask(evt) {
      evt.preventDefault();
      console.log(JSON.stringify(evt));
    },
    capture() {
      this.imageLoading = true;
      axios.get("/api/captureImage", {
        params: {
          device: this.device
        }
      }).then(({data}) => {
        this.image = data;
        this.imageLoading = false;
      });
    }
  }
}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
}
</style>
